import React from 'react';
import { Link } from 'react-router-dom';
import { sanitizeContent } from './helpers';
import ReactHtmlParser from 'react-html-parser';
// import sanitizeHtml from 'sanitize-html-react';

const ThePost = ({post, route}) => {

    // const posts = () => item.posts;
    // const item = posts()[index];

    // let linkPrefix = item.type === 'page' ? '/page/' : '/post/';
    let theContent = '',
        renderContent = '';

    switch(route) {
        case '/':
        case '/search/:term':
        case '/category/:catid':
            theContent = post.excerpt.rendered;
            break;
        default:
            theContent = post.content.rendered;
            break;
    }

    if (theContent !== '') {
        console.log('theContent', theContent);
        renderContent = sanitizeContent(theContent);
    }

    return (
        <div id={'post-id-'+post.id} className={'post-item'}>
            <h2><Link to={"/"+post.type+"/"+post.slug}>{post.title.rendered}</Link></h2>
            {/* <PostMeta index={index}></PostMeta> */}
            <div className="post-content">{ ReactHtmlParser(renderContent) }</div>
        </div>
    )
}

export default ThePost;