import React from 'react';
import axios from 'axios';

const storeContext = React.createContext();
export const Consumer = storeContext.Consumer;

export class Provider extends React.Component {
    constructor(props) {
        super(props);

        let route = this.props.match.params.route;
        let slug = this.props.match.params.slug ? this.props.match.params.slug : '';

        this.state = {
            slug: slug,
            route: route,
            posts: []
        }
    }

    getRestType() {
        switch(this.props.match.path) {
            case 'page':
            case 'post':
                return 'POST';
                break;
            default:
                break;
        }
    }

    buildUrl() {
        let url = '/wp-json/wp/v2/';
        // console.log('rest type', this.state.restType);

        switch(this.state.route) {
            case 'page':
                url += 'pages/?slug=' + this.state.slug;
                break;
            case 'post':
                url += 'posts/?slug=' + this.state.slug;
                break;
            default:
                break;
        }
        // console.log('URL', url);
        return url;
    }

    getPosts(url) {
        let self = this;
        // console.log("THIS", this, "\nthis.state:", this.state)

        axios.get(url)
            .then( res => {
                // console.log("RESPONSE",res.data)
                self.setState({
                    ...this.state,
                    posts: res.data
                })
            })
            .catch( err => {
                console.log(err);
                self.appError = 'An unexpected error occurred';
            })
    }

    componentDidMount() {
        // console.log('CONTEXT get posts', this.props);
        this.getPosts(this.buildUrl());
    }

    componentDidUpdate(prevProps) {
        // some stuff
    }

    render() {
        return (
            <storeContext.Provider value={this.state}>
                {this.props.children}
            </storeContext.Provider>
        )
    }
}